body {
  background-color: #121212;
  color: white;
  font-family: 'Poppins', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

.calculator {
  width: 350px;
  padding: 20px;
  background-color: #1e1e1e;
  border-radius: 20px;
  box-shadow: 0px 0px 20px rgba(0, 255, 255, 0.3);
}

.display {
  background-color: #000;
  color: #0ff;
  text-align: right;
  padding: 20px;
  font-size: 32px;
  font-weight: bold;
  border-radius: 10px;
  box-shadow: inset 0px 0px 10px rgba(0, 255, 255, 0.2);
  margin-bottom: 20px;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 15px;
}

button {
  padding: 20px;
  font-size: 20px;
  background-color: #1e1e1e;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 0px 0px 10px rgba(0, 255, 255, 0.2);
  transition: all 0.2s ease;
}

button:hover {
  background-color: #e0e0e0;
}

#clear {
  background-color: #ff9999;
}

#equal {
  background-color: #005c00;
}

button:nth-child(4n) {
  background-color: #29150100;
}

button#equal:hover {
  background-color: #00ffaa;
}

button#equal:active {
  background-color: #00ee99;
}

button#clear:hover {
  background-color: #ff3333;
}

button#clear:active {
  background-color: #ff0000;
}
